<template>
    <div class='newsDetailContent'>
        <Header  msg="新闻详情"/>
        <img src="../assets/img/news.png" alt="" class='banner' >
        <div class='content'>
            <h2>{{news.title}}</h2>
            <p>时间：{{news.createDate}}  撰稿人：娄锴</p>
            <div v-html="news.content"></div>  
        </div>
         <!-- 相关内容 -->
         <div class='recommed'>
          <p class="recom">相关内容</p>
            <div class="Cols" v-for="(item,index) in recommend " @click="toDetail(item.id,item.type)" >                    
                <div>
                    <img :src="item.imageUrl" alt="">
                    <h3>{{item.title}}</h3>
                    <p> {{item.intro}}</p>
                </div> 
            </div> 
         </div>
     </div>
</template>
<script>
import Header from './header'
 
export default { 
    components: {           
        Header,
         
    },  
    data(){
        return{
            news:'',//新闻详情
            recommend:'',//推荐新闻数据
         }
    },
    methods:{
        toDetail(id,type){
            this.$router.push('/newsDetail?id=' + id +'&type=' + type);
            this.result();
            this.$router.go(0)

        },
        result(){
            var that =this;
            this.newsId = this.$route.query.id;

            this.$axios.get('/api/v1/api/news/show?newsId='+that.newsId)
                .then(function (response) {                    
                        that.news = response.data.data;
                    
                    }).catch(function (response) {

            });  
        }
    },
    mounted(){
        this.result()
            var that =this;
            this.newsId = this.$route.query.id;
             //相关推荐
            this.$axios.get('/api/v1/api/news/recommend?newsId='+that.newsId)
                    .then(function (response) {                    
                            that.recommend = response.data.data;                        
                    })
                    .catch(function (response) {
                        
                });
                                      
    }
}


</script>
<style  lang="scss">
    .newsDetailContent{
        ul li{
            list-style:none;
        }
        .banner{
            width:100%;
            height:143px;
            margin-top: 40px;
        }
        .content{
            width:90%;
            margin:30px auto;
            img{
                width:100%;
            }
             h2{
                font-size:24px;
                font-family:PingFangSC-Semibold;
                font-weight:600;
                color:rgba(51,51,51,1);
                line-height:33px;
                text-align:left;
                margin-bottom:12px;
            }
            p{
                font-size:14px;
                font-family:PingFangSC-Regular;
                font-weight:400;
                color:rgba(155,155,155,1);
                line-height:21px;
                text-align:left;
                margin-bottom:29px;
            }
        }
        .recommed{
            width:90%;
            margin:30px auto;
            text-align:left;
            .recom{
                font-size:20px;
                font-family:PingFangSC-Regular;
                font-weight:400;
                color:rgba(89,89,89,1);
                line-height:28px;
                 margin-bottom:43px;            
            }
            .Cols{
                margin-bottom:20px;

            }
            img{
                width:100%;
                height:160px;
            }
            h3{
                font-size:16px;
                font-family:PingFangSC-Medium;
                font-weight:500;
                color:rgba(51,51,51,1);
                line-height:23px;
                 margin:8px auto 5px;
                white-space: nowrap;
                height: 20px;
                text-overflow: ellipsis;
                overflow: hidden; 
            }
            p{
                font-size:14px;
                font-family:PingFangSC-Light;
                font-weight:300;
                color:rgba(153,153,153,1);
                line-height:20px;
                text-overflow: ellipsis;
                overflow: hidden; 
                white-space: nowrap;
            }
        }
    }
</style>


