<template>
    <div id='navBox'>
        <nav class='nav'   @click="toTop()">          
            <img src="../assets/img/logo.png" alt="">
            <span style='margin-right:55px'>{{msg}}</span>            
            <!-- 侧边栏 -->
            <div class='aside' @click="showTitle()"   >
                <img src="../assets/img/ce.png" alt="">
             </div> 
        </nav>
        <div class='sideBox' v-show="showList">           
            <div  class='sideList'>
                <ul>
                    <li v-for="(name,index) in items[0]" >
                        <a :href="name.href">   
                            <div @click="showPage(index)">              
                                <p>{{name.name}}</p> 
                                <p  v-show='name.isSubShow' style='position: absolute;  right: 12px;  top:0px;'>
                                <span :ref="'icon-'+index">+</span>
                                </p>   
                            </div> 
                         </a>              
                        <ul  style="display:none" :ref="'show-'+index" >                   
                            <li v-for="(subItems,index) in name.subItems" @click="toPage(subItems.name,subItems.id,index)"  >
                                <a> <p>{{subItems.name}}</p> </a>
                            </li>
                        </ul>
                            
                    </li>
                </ul>
            </div>
        </div>
    </div> 
</template>

<script type="text/javascript">
 
export default {
   props: {
      msg: {
           type:String,
    }
  },
  data(){
    return{
        showList:false,
        show_list:false,
        dosName:'',
        items: [{
              index: {
                  name:"首页",
                  isSubShow: false,
                  href: '/index',
              },
              about:{
                  name:"关于我们",
                  isSubShow:false,
                  href: '/aboutUs',
              },
              news: {
                  name:"新闻动态",
                  isSubShow:false,
                  href: '/news'
                },
              disease:{
                  isSubShow:true,
                  name:"",
                  subItems:{},
                    
              },
              medication:{
                  isSubShow: true,
                  name:"",
                  subItems:{},
                  
              },
              tomore:{
                  name:"",
                  subItems:{},
                  isSubShow: true,
              },  
              partners: {
                  name:"招商合作",
                  isSubShow: false,
                  href: '/partners',
              },               
              
        }]
    }
  },
  methods:{
    // 显示侧边栏
    showTitle(){
      this.showList=!this.showList; 
        console.log(this.showList)
      if(this.show_list==true){     
  
        document.getElementById("app").style.position="fixed"; 
    }          
            // z-index: 1;
               
    },

   //显示二级列表
   showPage(index){
        var moreBtn = this.$refs['show-' + index][0] || null;   
        var icon = this.$refs['icon-' + index][0] || null;      
        if(moreBtn.style.display=="none"){
            moreBtn.style.display="block";
            icon.innerHTML='—'
         }else{
             moreBtn.style.display="none";
            icon.innerHTML='+'
        }              
   },
   //二级列表页面
    toPage(name,id){               
            this.$router.push('/diseases?id=' +  id +'&name='+name );   
            this.$router.go(0)

    },
    //点击导航回到顶部
    toTop(){
            var  scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop				 
            var timer = null;
            var  _that = this;
            cancelAnimationFrame(timer);
            timer = requestAnimationFrame(function fn() {
        
                if (scrollTop > 0) {
                    scrollTop -= 50;
                    document.body.scrollTop = document.documentElement.scrollTop =
                        scrollTop;
                    timer = requestAnimationFrame(fn);
                } else {
                    cancelAnimationFrame(timer);
                    // _that.goTopShow = false;
                }
            });     
       
    }
  
    
  },
  mounted(){
      
      var that =this;
      this.$axios.get('/api/v1/api/product/home'  )
      .then(function (response) {   
           that.dosName = response.data.data ;
          that.items[0].disease.subItems=that.dosName[0].content;
          that.items[0].disease.name=that.dosName[0].name;
          that.items[0].medication.subItems=that.dosName[1].content;
          that.items[0].medication.name=that.dosName[1].name;
          that.items[0].tomore.subItems=that.dosName[2].content
          that.items[0].tomore.name=that.dosName[2].name

      })
      .catch(function (response) {
          console.log(response);
      });  
  }

}
</script>

<style lang="scss" >
 
    
.nav{
    z-index:1100;
    max-width:750px;
    width: 100%;
    height:40px;
    line-height:40px;
    text-align:center;
    color:rgba(255,255,255,1);
    font-size:17px;
    background:rgba(21,37,103,1);
    position:fixed;
    img{
        width: 46px;
        height: 23px;
        float: left;
        padding: 8px 0 0 15px;
    }  
    }
    .sideBox{
        z-index: 100;
        width: 100%;
        overflow: scroll;
        position: fixed;
        top: 0;
        bottom: 0;
        overflow-y: scroll;
        height: 100%;
         max-width: 750px;
     }
    .aside{
        position: absolute;
        top: 0;
        right: 15px; 
        color:red;   
        img{
        width: 20px;
        height: 15px;
        margin-top: 5px;
        }
    }
    .sideList{
    margin-top:39px;
    width: 100%;
    height: -webkit-fill-available;
    background: #fff;       
    ul{
        list-style:none;
        background:white;
        padding:0;
        margin:0;
       li:last-child{
             border-bottom: 1px solid #B1B1B1;
       }
        li{
         background:#fff;         
        border-top: 1px solid #B1B1B1;
        width: 90%;
        margin: 0 auto;
        
        div{
            height:61px;
            position: relative;
            text-align: left; 
            line-height:61px;
        span{
            font-size:20px;
        }
        }
        ul{
             width:100%;
            text-align: left;
           li:last-child{
             border-bottom: none
        }
            li{
                width: 100%;
                line-height: 42px;
                p{
                     font-family:PingFangSC-Regular;
                    font-weight:400;
                    color:rgba(102,102,102,1);
                    display: inline-block;
                    margin-left: 18px;
                    }
                }
            }
        p{
            font-size:15px;
            font-family:PingFangSC-Regular;
            font-weight:400;
            color:rgba(102,102,102,1);
            display: inline-block;
        }
    }
}
}
    
</style>



