<template>
    <div class='deasesContent'>
        <Header  :msg="result.typeName"/>
       
        <!-- 市场潜力需求大 -->
        <Circular :msg="result.title" class="deaseCircular" />
        
        <div class="titleText">
            <p class='name' v-html='result.intro'></p>
            <p style='height:20px'></p>
            <div v-for="(item,index) in result.contents">
                
                <p v-html='item.content'></p>
                <img :src="item.imageUrl"> 
            </div>
        </div>
        
    </div>
</template>
<script>
import Header from './header'
import Circular from './circular'


export default { 
    components: {          
        Header,
        Circular
    },  
    data(){
        return{
            result:'',

         }
    },
    watch:{
        '$route'(){
            this.datas()
        }
    },
    methods:{
     datas(){
            var that =this;
            var id = this.$route.query.id;
            this.name =this.$route.query.name;  
            
            this.$axios.get('/api/v1/api/product/show?productId='+id  )
                .then(function (response) {    
                        that.result=response.data.data ;
                    })
                  .catch(function (response) {
                  console.log(response);
                  }); 

     }
 

      },
    mounted(){
          this.datas()
    }
}

</script>
<style  lang="scss">
.deasesContent{
    .deaseCircular{
        margin-top:0px !important;
        padding-top: 34px;   
    }
    .banner{
        width:100%;
        margin-top: 40px;
    }
    .titleText{
        width:90%;
        margin:0 auto;
        p{
            font-size:14px;
            font-family:PingFangSC-Light;
            font-weight:300;
            color:rgba(89,89,89,1);
            line-height:25px;
            // text-align:left;
            letter-spacing:2px;
        }
        img{
            margin-top:10px;
            width:85%;
           
        }
    }
  
}
</style>


