import Vue from 'vue'
import Router from 'vue-router'
import index from '../components/index'
import aboutUs from '../components/aboutUs'
import partners from '../components/partners'
import news from '../components/news'
import newsDetail from '../components/newsDetail'
import joinUS from '../components/joinUS'
import jobDetails from '../components/jobDetails'
import diseases from '../components/diseases.vue'
import submitJob from '../components/submitJob.vue'
 import header from '../components/header.vue'
import showZZ from '../components/showZZ.vue'

import Footer from "../components/Footer.vue"

Vue.use(Router) 
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/index',
      name: 'index',
      component: index
    },
 
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: aboutUs
    },
    {
      path: '/partners',
      name: 'partners',
      component: partners
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/newsDetail',
      name: 'newsDetail',
      component: newsDetail
    },
    {
      path: '/diseases',
      name: 'diseases',
      component: diseases
    },
    {
      path: '/joinUS',
      name: 'joinUS',
      component: joinUS
    },
    {
      path: '/jobDetails',
      name: 'jobDetails',
      component: jobDetails
    },
    {
      path: '/submitJob',
      name: 'submitJob',
      component: submitJob
    },
    {
      path:'*',
      redirect:'/index'
   }
  ]
})



