<template>
        <div class='jobDetailContent'>
            <Header  msg="职位详情"/>
            <img src="../assets/img/joinUs.png" alt="" class='banner' >
            <div class='content'>
                <p class='position'>{{data.position}}
                    <span>{{data.salaryName}}</span>
                </p>
                <p>{{data.workPlace}}/{{data.educationName}}/{{data.workYearsName}}</p>
            </div>
            <p class='line'></p>
            <div class="content"> 
                <p class='position'>岗位描述：</p>                    
                <p  v-html='data.description'> </p>
            </div>
             <div class="content" style='margin:20px auto'>  
                <p class='position'>岗位要求：</p>                    
                <p  v-html='data.requirement'> </p>
            </div>
            <button class='button' @click="dialogVisible()">投递简历</button>
        </div>
</template>
<script>
import Header from './header'
import Circular from './circular'
import { MessageBox } from 'mint-ui';

export default { 
    components: {          
        Header,
        Circular,
        MessageBox
    },  
    data(){
        return{
            data:'',
         }
    },
    
    methods:{
        dialogVisible(){
             // 弹出框
         MessageBox.alert('',{
                title:' ',
                message:'<div>请把简历发送至“hr@1gene.com.cn”邮箱</div>',
               
            }).then(action => {
               
               
            }) 
              
           

        }
 

      },
    mounted(){
        this.jobId = this.$route.query.id;
        var that =this;
        this.$axios.get('/api/v1/api/job/show?jobId='+that.jobId  )
            .then(function (response) {    
                that.data = response.data.data ;
                 
            })
            .catch(function (response) {
         });

       
    }
}

</script>
<style  lang="scss">
.jobDetailContent{
    
    .banner{
        width:100%;
        margin: 40px auto 50px;
 
    }
    .content{
        width:90%;
        margin:0 auto;  
         p{
            font-size:14px;
            font-family:PingFangSC-Light;
            font-weight:300;
            color:#999999;
            line-height:25px;
            text-align:left;
            letter-spacing:2px;
        }
        .position{
            color:#666666;
            font-weight:500;
            font-size:16px;
            span{
                float:right;
            }
        }
     }
     .line{
         width:90%;
         margin:10px auto 20px;
         background:#B1B1B1;
         height:1px;
     }
    .button {
        width: 130px;
        height: 33px;
        background: #1251D0;
        color: #fff;
        border: 2px solid #1251D0;
        margin: 30px 0 30px 0;
        border-radius:4px;
        font-size:14px;
    }
  
}
</style>


