<template>
        <div class='joinUsContent'>
            <Header  msg="加入我们"/>
            <img src="../assets/img/join/banner1.png" alt="" class='banner' >
            <!-- 招聘职位 -->
            <Circular msg="招聘职位" />
            <div class='position'>
                <div class='jobContent'>           
                    <div v-for='job in jobResult'>
                        <h2>{{job.position}}</h2>
                        <p>工作地点：{{job.workPlace}} / 招聘人数：{{job.number}}人</p>
                        <a @click="toJobDetail(job.id)">了解详情 &gt;</a>
                    </div>
                 </div>
            </div>
              <!-- 员工活动 -->
            <Circular msg="员工活动" />
            <div v-for='active in employeesList' class='employees'>
                <img :src="active.imgUrl" alt="">
                <p class='title'>{{active.title}}</p>
                <p class='desc'>{{active.desc}}</p>
            </div>
        </div>
</template>
<script>
import Header from './header'
import Circular from './circular'


export default { 
    components: {          
        Header,
        Circular
    },  
    data(){
        return{
            jobResult:'',
            employeesList:[
                {"imgUrl":require("../assets/img/join/1.png"),"title":"员工生日趴","desc":"定期为当月生日员工举办集体生日趴，让员工度过一个不一样的生日，增强员工集体归属感。"},
                {"imgUrl":require("../assets/img/join/2.png"),"title":"素质拓展","desc":"定期举办员工素质拓展活动，增加公司凝聚力。同时公司开设篮球 羽毛球等兴趣班，丰富员工业余生活。"},
                {"imgUrl":require("../assets/img/join/3.png"),"title":"活动室及下午茶","desc":"活动室配备专业台球桌 乒乓球桌及健身器材，供员工休息时使用。每天提供免费下午茶福利。"},
            ]
         }
    },
    methods:{
        toJobDetail:function(id){
                 this.$router.push({                        
                  name: 'jobDetails',  
                  query: {   
                        id: id,   
                        
                  }                      
              })
        }
 
      },
    mounted(){
         var that =this;
            this.$axios.get('/api/v1/api/job/list'  )
                .then(function (response) {    
                    that.jobResult = response.data.data ;              
          
                })
                .catch(function (response) {
                    console.log(response);
                });
   
        

    }
}

</script>
<style  lang="scss">
.joinUsContent{
    .banner{
        width:100%;
        margin-top: 40px;
    }
    .position{
        width:90%;
        margin:0px auto;
        box-shadow:0px 2px 16px 0px rgba(0,0,0,0.11);
        border-radius:5px;
        .jobContent{
            padding-top:20px;
            padding-bottom:20px;
            div{
                height:65px;
                width:90%; 
                margin:0px auto;
                border-bottom:1px solid #b1b1b1;  
                text-align:left; 
                line-height:30px;
                h2{
                    font-size:16px;
                    font-family:PingFangSC-Medium;
                    font-weight:500;
                    color:rgba(102,102,102,1);
                    line-height:26px;
                    text-align:left;
                }
                p{
                    display: inline-block;
                    color:#999999;
                }
                p:first-child{
                    font-size: 9x;                                            
                }
                a{
                    color:#666666;
                    float:right;
                }
             }
        }
      
    }
    .employees{
        width: 90%;
        margin: 0 auto;
        text-align: left;
        img{
            width: 100%;
           height: 185px;
           border-radius:5px;
        }
        .title{
            font-size:15px;
            font-family:PingFangSC-Medium;
            font-weight:500;
            color:rgba(51,51,51,1);
            line-height:16px;
            margin:15px auto 5px;
         }
        .desc{
            font-size:14px;
            font-family:PingFangSC-Regular;
            font-weight:400;
            color:rgba(153,153,153,1);
            line-height:25px;
            margin-bottom:20px;
        }
    }
}
</style>


