     </div>
</template>
<script>

 
 
import Header from './header'
import showZZ from './showZZ'
 
import Circular from './circular'
import { Field } from 'mint-ui';
import { Toast,Indicator,MessageBox } from 'mint-ui';
import VDistpicker from 'v-distpicker'
import myaddress from '../assets/address.json'   

export default { 
    components: {          
        Header,
        showZZ,
        Circular,
        Field,
        Field:"Field.name",
        Toast,
        MessageBox,
        Indicator,
        VDistpicker  
 
    },  
    data(){
        return{
            jobResult:'',
            text:[               
                    {img:require("./../assets/img/partner/jiankang.png"),title:'健康观念转变',desc:'随着生活水平的不断提高，人们对疾病的关注重心逐渐从“治疗”转向“筛查”和“预防”。基于基因检测的“4P医学模式”（预测、预防、个体化和主动参与）成为时代的新焦点。'},
                    {img:require("./../assets/img/partner/shichanghuodong.png"),title:'市场需求庞大',desc:'未来5年内，我国认识到基因检测重要性的人群将达到总人口的15%-20%，会有约3亿人的市场潜在需求，每年基因检测量至少在300万人次以上，且逐年递增。'},
                    {img:require("./../assets/img/partner/kuaisudao.png"),title:'行业发展迅猛',desc:'2018年国内基因检测行业市场规模已达207亿元，同比增长31%，为全球增速最快的国家之一。预测到2021年将达421亿元，在未来的十年内，将会构建一个千亿级的市场。'}
                ],
            imgList: [{id:0,idView:require("./../assets/img/partner/Image5.png"),time:"2019年7月 国家卫健委:《健康中国行动（2019—2030年）》",content:'<div>强调针对高发癌种，要制定筛查和早诊早治指南。到2022年和2030年，高发地区重点癌种早诊率达到55%及以上并持续提高，基本实现癌症高危人群定期参加防癌体检。</div>'},
                  {id:1,idView:require("./../assets/img/partner/Image4.png"),time:"2017年4月 科技部：《“十三五”生物技术创新专项规划》",content:'<div>重点发展新一代基因测序技术，开发一批疾病防控、临床诊治生物靶标和生物标志物，加快技术在疾病防控、临床诊治和生物制造、品种创制、新药开发等领域的应用。</div>'},
                  {id:2,idView:require("./../assets/img/partner/Image3.png"),time:"2017年1月 国家发改委：《“十三五”生物产业发展规划》",content:'<div>以个人基因组信息为基础，利用基因测序、大数据分析等手段，在肿瘤、遗传性疾病等方面实现精准的预防、诊断和治疗。对特定患者量身设计最佳诊疗方案，达到个体化治疗的目的。并明确了基因检测能力覆盖50%以上出生人口的目标。</div>'},
                  {id:3,idView:require("./../assets/img/partner/Image2.png"),time:"2015年6月 国家发改委：《国家发展改革委关于实施新兴产业重大工程包的通知》",content:'<div>充分发挥基因检测等新型医疗技术以及现代中药在疾病防治方面的作用，提升群众健康保障。</div>'},
                  {id:4,idView:require("./../assets/img/partner/Image1.png"),time:"2015年3月 科技部：“中国精准医疗计划",content:'<div>到2030年前，我国将在精准医疗领域投入600亿元，其中，中央财政支出200亿元，企业和地方财政配套400亿元。</div>'}],
           company:[
                {title:'国际领先的实验室：',content:'拥有1300平米的独立医学检验实验室配置了国际领先的基因检测仪器，具备行业一流的基因检测软硬件平台。'},
                {title:'公司资质齐全：',content:'拥有科技部、卫健委和中国质量认证中心等多个部门认证颁发的五大资质，包括国家高新技术企业、医疗执业机构许可、临床基因扩增实验室资质、满分通过卫健委室间质评和ISO9001质量管理体系认证。'},
                {title:'地方政策鼎力支持：',content:'杭州各级政府部门重点支持的企业，获得“5050计划” 1000万顶格支持，同时也是上市公司远方信息（300306）旗下的品牌。'},
                {title:'强大的研发团队：',content:'拥有以医学、遗传学、生物信息学博士为主的强大研发团队，先后获得国家发明专利和软件著作权等知识产权50多项。'},
                {title:'众多医院合作：',content:'目前已与国内众多三甲医院建立了良好的合作关系，并与许多著名专家开展了多个合作项目，产品得到了临床专家的广泛认可。'}
           ],
         cooperation_advantages:[
            {img:require("./../assets/img/partner/01.png"),desc:'产品种类齐全，差异化显著，更新升级快，客户体验好。'},
            {img:require("./../assets/img/partner/02.png"),desc:'无需经验从“零”开始，提供全套、多次培训，让非专业人员快速熟悉产品。'},
            {img:require("./../assets/img/partner/03.png"),desc:'提供折页、海报、H5页面等定制化宣传资料设计，让推广过程更加简便、流畅。'},
            {img:require("./../assets/img/partner/04.png"),desc:'保护区域代理商利益，统筹价格体系，设置独家代理。'},
            {img:require("./../assets/img/partner/05.png"),desc:'公司打造百万粉丝公众号，客户按区域导流给合作伙伴，帮助提高销售业绩。'},
            {img:require("./../assets/img/partner/06.png"),desc:'专业的市场、IT团队配合大客户拜访，协助组织地方推广活动，提供营销工具平台。'},
            {img:require("./../assets/img/partner/07.png"),desc:'区域合作伙伴重点扶持，分享营销模式、成功案例。'},
            {img:require("./../assets/img/partner/08.png"),desc:'医生、营养师专业团队为您的客户提供一对一免费解读、咨询服务。'}
             
         ],
            
            ruleForm: {
                name: '',
                desc: '',
                tel:'', 
                add:''        
            },
            addInp:false,
            showAddess:false,
            state:' ',//校验
            statetel:'',
            statedesc:'',
            stateADD:'',
            nameError:'',
            telError:'',
            descError:'',
            addressError:'',
            addErro:false,
            inputErrorMessage:{
                name:"请填写名字",
                tel:"请填写号码",
                desc:'请写留言',
                add:"请填写地址"
            }, 
           province:'',
           city:'',
           area:'',
            show:false,
             myAddressSlots: [
                    {
                        flex: 1,
                        defaultIndex: 0,        
                        values: Object.keys(myaddress),    //省份数组
                        className: 'slot1',
                        textAlign: 'center'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: this.getCity('北京市'),
                        className: 'slot3',
                        textAlign: 'center'
                    },
                    {
                        divider: true,
                        content: '-',
                        className: 'slot4'
                    },
                    {
                        flex: 1,
                        values: this.getarea('北京市','市辖区'),
                        className: 'slot5',
                        textAlign: 'center'
                    }
                ],
                myAddressProvince:' ',
                myAddressCity:' ',
                myAddresscounty:' ',
                show_zz:false,
                scrollTop:'',
          
          }
    }, 
    methods:{
          // 地址选择
        onMyAddressChange(picker, values) {
               if(this.myAddressProvince=="" ){
                this.addErro=true              
             }else{
                 this.addErro=false                 
             }
            if(myaddress[values[0]]){    //这个判断类似于v-if的效果（可以不加，但是vue会报错，很不爽）
                picker.setSlotValues(1,Object.keys(myaddress[values[0]])); //  Object.keys()会返回一个数组，当前省的数组
                picker.setSlotValues(2,myaddress[values[0]][values[1]]); //  区/县数据就是一个数组
                this.myAddressProvince = values[0];
                this.myAddressCity = values[1];
                this.myAddresscounty = values[2];
            }
        },

        choose(){
            this.show=!this.show
        },

        getCity:function(pro){
             return Object.keys(myaddress[pro])
        },

            //这个就是初始化逻辑，其实就是获取省下的某个市下的区，取决于你的json数据格式
        getarea:function(pro,city){
            return myaddress[pro][city]
        },
 
        //点击打开城市选择
        checkadd(){
            this.addInp=!this.addInp;
            this.show_zz=!this.show_zz;
            this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop				             
            document.getElementById("app").style.top=-this.scrollTop+"px"   
            document.getElementById("app").style.position="fixed";
          },

        //  关闭选择
         chooseCity(){
             this.show_zz=!this.show_zz;
            this.addInp=!this.addInp;         
            document.getElementById("app").style.position="static";
            window.scrollTo(0,this.scrollTop)
          },
        //检查姓名
        checkName(){
            var top = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            window.scroll(0, top)
            var value= this.ruleForm.name
            //判断名字中必须包含字符串
            if(/.*[\u4e00-\u9fa5]+.*$/.test(value)){
              this.nameError=''
            }else{
                 this.nameError=this.inputErrorMessage.name
            }                  
        },
        //检查电话
        checkTel(){
            var top = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            window.scroll(0, top)
            var length=this.ruleForm.tel.length 
            if(length == 11 && /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(14[0-9]{1})|)+\d{8})$/.test(this.ruleForm.tel)){
                     this.telError=''
            }else if(length>0 && length<11 || length>11){
                  this.telError="请填写正确的手机号码"
            }else{
                this.telError=this.inputErrorMessage.tel
            }
          },
  
        //检查留言
        checkdesc(){
            var top = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            window.scroll(0, top)
            if(this.ruleForm.desc.length>0){
                    this.descError=''
                }else{
                    this.descError=this.inputErrorMessage.desc
            } 
         },
        //提交数据
        leave(){
            this.checkName()
            this.checkTel()
            this.checkdesc()
             if(this. myAddressProvince.length>1){
                   this.addErro=false
            } else{
                  this.addErro=true
              }
          
            if(this.ruleForm.name && this.ruleForm.tel && this.ruleForm.desc && this. myAddressProvince && this.myAddressCity && this.myAddresscounty){
                     var that = this;
                    var data = {
                        name: this.ruleForm.name,
                        phone: this.ruleForm.tel,
                        address:this.myAddressProvince + this.myAddressCity + this.myAddresscounty,
                        content: this.ruleForm.desc,
                    }
                     this.$axios({
                            method:'post',
                             url: '/api/v1/api/mail/cooperation',
                             data:data
                          
                        }).then((res) => {
                              if (res.data.code==200) {
                                  that.messageAlert()
                                 console.log("成功")                 
                             }

                        });

            }else{
                console.log("提交失败")
            } 

        },
        messageAlert(){
            MessageBox.alert('发送成功').then(action => {
                this.$router.go(0)
            })
         

        }
     },
    mounted(){
             this.$nextTick(() => {  //vue里面全部加载好了再执行的函数   （类似于setTimeout）
                this.myAddressSlots[0].defaultIndex = 0   
                 
                 // 这里的值需要和 data里面 defaultIndex 的值不一样才能够初始化
                //因为我没有看过源码（我猜测是因为数据没有改变，不会触发更新）
            });
            
        }

    }
 

</script>
<style  lang="scss">
                
