        .title{
            font-size: 15px;
            color: #1251D0;
            letter-spacing: 0;
            text-align: center;
            line-height: 17px;
            margin-top:11px;
        }
        .desc{
            font-size: 14px;
            color: #666666;
            letter-spacing: 0;
            text-align: justify;
            line-height: 25px;
            margin-bottom:15px;
        }
        img{
            margin-top:10px;
             height:55px;
        }
    }
    .country{
        width:90%;
        margin:0 auto 40px; 
          font-family:PingFangSC-Semibold;
        img{
            width:345px;
            height:145px;
        }
       .name{
           margin-bottom:20px;
            font-size:17px;            
            font-weight:600;
            color:rgba(51,51,51,1);
            line-height:25px;
        }
        .desc{
             margin-top:20px;
            font-size:14px;
          
             color:#6B6B6B;
            line-height:25px;
        }
    }
    .company_img{
        width:345px;          
    }
    .brand{
        width:90%;
        margin:25px auto;
        font-size: 14px;
        color: #666666;
        letter-spacing: 0;
        text-align: justify;
        line-height: 25px;
        span{           
            color: #1251D0;            
        }

    }
    .cooperation{
            width: 90%;
            height:385px;
            margin: 0 auto;
            box-shadow: 0 0 20px 0 rgba(0,0,0,0.10);
            border-radius: 5px;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        .box{
           width:308px;
           height:348px;
           margin:0 auto;
           .title{
                margin:16px auto 7px;
                width:100%;
                height:33px;
                line-height:33px;
                background: #70DFBF;
                font-size: 12px;
                color: #FFFFFF;
                letter-spacing: 0.93px;
           }
          
            img{
               height:200px;
               width:100%;
               margin-bottom:14px;
            }
            div{
                border:1px solid #70DFBF;
                height:100px;
                span{
                    display: block;
                    font-size: 16px;
                    color: #70DFBF;
                    letter-spacing: 1.24px;
                    text-align: center;
                    margin:10px auto 8px;
                } 
                p{
                    font-size: 13px;
                }
            }

        } 
  

    }
    .iconImg{
        width:52px;
        height:35px;
        margin:18px auto  
    }
    .cooperation_box{
        width: 92%;
        margin:0 auto;
        .all_cooperation{
                display:flex;
                flex-wrap: wrap;
                justify-content: space-between;
