                height:235px;
                width:167px;
                box-shadow: 0 0 20px 0 rgba(0,0,0,0.10);
                border-radius: 5px;
                margin-bottom:10px;
            }
            img{
                width:100%;
            }
            p{
                width: 121px;
                height: 50px;
                padding-top: 10px;
                font-size:10px;
                color: #666;
                letter-spacing: 0;
                text-align: justify;
                line-height: 16px;
                margin: 0 auto;
            }
        }        
    }
    .to_join{
        width:90%;
        margin:0 auto;
        font-size: 14px;
        color: #666666;
        letter-spacing: 0;
        text-align: center;
        line-height: 25px;
        margin-bottom:10px;
    }
    .join_box{
        width:345px;
        margin:0 auto;
        background: #FFFFFF;
        box-shadow: 0 0 20px 0 rgba(0,0,0,0.10);
        border-radius: 3px;
        height:386px;
        .title{
            width: 325px;
            text-align: left;
            margin: 12px 0 10px 12px;
            font-size: 12px;
            color: #000000;
            letter-spacing: 1.04px;
            line-height:16px;
            .tiao{
                width: 0.133333rem;
                height: 0.346667rem;
                background: #1251d0;
                display: inline-block;
                vertical-align: text-bottom;
                margin: 23px 7px 0 0;
            }
            .blue{
                font-size: 15px;
                color: #1251d0;
                 
                line-height: 21px;
                margin-right: 9px;
            }
        }
        ul{
            width:325px;
            margin:0 auto;
            display:flex;
            flex-direction: column;
            li{
                list-style:none;
                font-size:12px;
                color: #666;
                letter-spacing: 0;
                text-align: justify;
                line-height:21px;
                span{
                    margin-right: 9px;
                    color: #00f;
                    font-size: 15px;
                    
                }
            }
        }
        .knowMore{
            width: 117px;
            margin: 12px auto 0;
            height: 37px;
            border: 1px solid #1251d0;
            color: #1251d0;
            text-align: center;
            line-height: 37px
        }
        img{
            width:325px;
            height:170px;
            margin-bottom:11px;
        }
    }
    .adImg{
        margin-top:40px;
        position:relative;
        img{
            width:345px;
            height:167px;

        }
        a{
            position:absolute;
            width: 30px;
            height: 18px;
            bottom: 32px;
            left: 173px;
        }
    }
    .contact{
        width:90%;
        margin:0 auto;
        .input{
            display: flex;
            justify-content: space-between;
            
            a{
                float: left;
                width: 165px;
            
            }
            p{
                text-align:left;
                color:red;
            }
        }
        .textL{
            text-align: left;
            color: red;
            margin-top: 10px;
        }
         .mint-cell{
            border:1px solid #dcdfe6;
            margin:10px 0px;
        }
        .mint-field-clear{
            display:none
        }
        .is-textarea{
            clear:both;
        }
        .mint-field-core{
            font-size: 12px;
        }
        .mint-field-other{
            font-size: 11px;
        }
       

    }
     .mint-button {
            width:90%;
            margin:10px auto;
        }
    .distpicker-address-wrapper{
        height: 200px;
        overflow-y: scroll;
        position: fixed;
        z-index: 100;
        bottom: 0;
        width: 100%;
    }
    .partner .distpicker-address-wrapper{
        position: fixed;
        z-index: 100;
        bottom: 0;
        width: 100%;
    }
    .distpicker-address-wrapper .address-header{
        height: 43px;
    }
    .distpicker-address-wrapper .address-container{
        height: 158px;
        overflow-y: scroll;
    }
    .chooseAdd{
        height:50px;
        line-height:50px;
        padding-left:10px;
        border:1px solid #dcdfe6;
        text-align:left;
       
     }
     .picker{
         border:1px solid #dcdfe6;
         border-top:none;
     }
   
     .prop-tk{
         z-index: 2300;
        height: 203px;
        background: #fafafa;
        position: fixed;
        left: 0;
        width: 100%;
        bottom: 0;
        .pop-btn{
            height: 30px;
             width: 100%;
            z-index: 2000;
        }
        
     }
     .picker-items{
           
        
        height: 462px;
        position: relative;
        .picker-slot{
            overflow: hidden;
            position: relative;
        }
        .picker-slot-wrapper{
             height: auto !important;
            margin-top: -71px;
        }
        .picker-slot.picker-slot-divider{
            margin-top: -212px;
        }
     }
     .picker-center-highlight{
         top: 20px;
     }
     .clR {
        display: flex;
        justify-content: space-around;
        padding-top: 10px;
        font-size: 14px;
      }
    
    .picker-slot{
           font-size:15px !important
      }
      .mint-cell-wrapper{
          background:none
      }
   
     
 }
</style>

 


