<template>
    <div class='indexBox'>
        <Header  msg="首页"/>
        <!-- 轮播 -->
        <div id="content">                      
            <div class='swiperBox'>           
                <mt-swipe :auto="2000">
                     <mt-swipe-item class='bannerImg' v-for="(img,index) in bannerList"    :key="img.id" :style="{backgroundImage: 'url(' + img.backImageUrl + ')' }">
                        <a>
                            <img :src="img.imageUrl" alt="">
                        </a>
                    </mt-swipe-item>
                </mt-swipe> 
             </div>
            <!-- 中间图片
            <div>
                <div class='content_img' v-for="item in imgLists" :key="item.id" >
                    <img :src="item.idView" alt="">
                     <p class='imgName'>{{item.imgName}}</p>
                     <p class='imgDesc'>{{item.desc}}</p>
                </div>
            </div> -->
            <!-- 新闻动态-->
              <Circular msg="新闻动态" />
              <div class='news'>
                <img :src="imageUrl" alt=""> 
                <div v-for='news in result'>
                    <div class='newsBox' @click="toDetail(news.id,news.type)">                                     
                        <h3>{{news.title}}</h3>
                        <p >{{news.intro}}</p>
                    </div>
                </div>
                 <a href="/news">
                    <p class='showMore'>查看更多</p>
                 </a>
              </div>
              <!-- 平牌故事 -->
              <div class='blue'></div>
              <div class='aboutUs'>
                 <Circular msg="关于我们"  style='margin-top:-34px;' />
                 <p>壹基因是权威的癌症基因检测机构，利用世界先进的基因检测技术和基因大数据计算技术，为大众提供癌症早期筛查、提前预防和精准治疗指导的基因检测服务。公司以“让每个人更早地远离癌症威胁”为使命，以 “防癌基因检测领军专家”为定位，不断开发和优化癌症基因检测技术，帮助每个人赢得癌症防御战。</p>
                <p class='toAboutUs'> <a href="/aboutUs">查看更多</a> </p>
              </div>
              <!-- 视频宣传 -->
                <Circular msg="视频宣传" /> 
                <div class="videos" v-for="(vid,index) in video" :key="index" v-if="index<4" >               
                    <div >                                                                   
                        <video                    
                        id='video'    
                        controls  
                        controlslist="nodownload"
                        :poster="vid.imageUrl"                                                                                                           
                        :src="vid.url" type="video/mp4"
                        >                                           
                            <p>很抱歉！您的浏览器不支持视频控件请更新版本/使用uc浏览器等</p>                         
                        </video> 
                        <p  style='margin-top: 13px; color: #888888;text-align:center'>{{vid.title}}</p>
                    </div>                                 
                </div>           
                <!-- 加入我们 -->
                <Circular msg="加入我们" /> 
                <div class='joinUs'>
                    <div style='padding-top:10px;width:100%;height:100%'>              
                        <div v-for='(job,index) in jobResult' v-if="index<3"  class='jobContent'>
                            <p>{{job.position}}</p>
                            <p>工作地点：{{job.workPlace}} / 招聘人数：{{job.number}} <span  @click="toJobDetail(job.id)">查看 ></span> </p>
                        </div>
                        <a href="./joinUs"><p class='showMore'>了解更多</p></a>

                    </div>
                </div>
                <!-- 合作伙伴 -->
                <Circular msg="合作伙伴" />
                <div class="partner">
                    <div ><img src="../assets/img/index/partner/1.png" alt=""></div> 
                    <div><img src="../assets/img/index/partner/2.png" alt=""></div> 
                    <div ><img src="../assets/img/index/partner/3.png" alt=""></div> 
                    <div><img src="../assets/img/index/partner/4.png" alt=""></div> 
                    <div><img src="../assets/img/index/partner/5.png" alt=""></div> 
                </div> 
                 <!-- 联系我们 -->
                <Circular msg="联系我们" />
                <div class='contact'>
                    <p class='name'>杭州和壹基因科技有限公司</p>
                    <p class='address'>健康检测：400-163-5588</br> health@1gene.com.cn</p>
                    <img src="../assets/img/ewm.jpg" alt="">
                    <p class='address'>精准医疗：0571-87885730</br>healthcare@1gene.com.cn</p>
                    <img src="../assets/img/ewm.png" alt="">
                    <p class='address'>前台电话：0571-87188266 </p>
                    <p class='address'>地址：浙江省杭州市滨江区聚才路88号</p>
                </div>
            </div>
        </div>
</template>
<script>
import { Swipe, SwipeItem } from 'mint-ui';  
import 'mint-ui/lib/style.css'      
import Circular from './circular'
import Header from './header'


export default {
 components: {  
        Header,   
    'mt-swipe': Swipe,
    'mt-swipe-item': SwipeItem,  
    Circular
    },  
  data(){
    return{
        bannerList:"", 
        result:'',//新闻数据
        imageUrl:'',
        video:'',//视频数据
        jobResult:'',//加入我们
        imgLists: [{id:0,idView:require("../assets/img/index/卡片1.png"),name:'癌症遗传风险基因检测',imgName:'疾病预防',desc:'对疾病遗传风险检测，有助于采取科学、系统、个体化的预防和干预措施，真正意义上实现。'},
                {id:1,idView:require("../assets/img/index/卡片2.png"),name:'慢性病精准用药指导基因检测',imgName:'精准用药',desc:'对肿瘤患者特定靶标基因检测，分析药物疗效及毒副作用的差异，为临床治疗提供最优方案。'},
                {id:2,idView:require("../assets/img/index/卡片3.png"),name:'无创肠癌早期筛查基因检测',imgName:'早期筛查',desc:'选取与癌症特异性强的标志物，可在早期发现病变，具有无创、准确、快速、简便等诸多优点。'}]
         }
    },
  
  methods:{
        toDetail(id,type){
            this.$router.push({                        
                name: 'newsDetail',  
                query: {   
                    id: id,  
                    type:type 
                 }                      
            })
        },
       // 工作详情
      toJobDetail:function(id){
             this.$router.push({                        
                name: 'jobDetails',  
                query: {   
                    id: id,   
                 }                      
            })
      },
  },
  mounted(){
      
      var that =this;    
    //轮播图
      this.$axios.get('/api/v1/api/banner/home/mobile'  )
      .then(function (response) { 
          that.bannerList=response.data.data  
        })
      .catch(function (response) {
          console.log(response);
      });  
      //新闻列表
    this.$axios.get('/api/v1/api/news/home')
        .then(function (response) {         
            that.result = response.data.data; 
            that.imageUrl=that.result[0].imageUrl
     })
    .catch(function (response) {
    });
      //视频列表
    this.$axios.get('/api/v1/api/video/home')
        .then(function (response) {         
            that.video = response.data.data; 
                  
    })
    .catch(function (response) {
    });
   //加入我们
    this.$axios.get('/api/v1/api/job/list')
        .then(function (response) {         
            that.jobResult = response.data.data; 
                  
    })
    .catch(function (response) {
    });

  }

}
</script>
<style lang="scss" > 
    .swiperBox .mint-swipe{
       height:203px;
       width:100%;
        padding-top: 40px;
        .bannerImg{
            width:100%;
            height: 203px;
            background-size:100% 100%;       
            img{
                 height:203px;
            }
         }
    }
    .content_img{
        width:90%;
         margin:19px auto;
        box-shadow:0 0 20px 3px rgba(177,177,177,0.5);
        border-radius:8px;
        position:relative;

        img{
            width:100%;
        }
        .imgName{
            position: absolute;
            top: 36px;
            left: 34px;
            font-size: 20px;
            color: #8B8A8A; 
            letter-spacing: 1.33px;
        }
        .imgDesc{
            position: absolute;    
            top: 85px;
            width: 120px;
            left: 34px;
            font-size: 8px;
            color: #8B8A8A;
            letter-spacing: 0;
            text-align: justify;
            line-height: 17px;
        }
    }
    .news{
         width:90%;
        margin:0  auto 20px;
        background:#FFFFFF;
        height: 726px;
        box-shadow:0px 2px 16px 0px rgba(0,0,0,0.11);
        border-radius:5px;
        img{
            width:321px;
            height:143px;
            margin:18px auto;
        }
        .newsBox{
              border-bottom:1px solid  #ccc;
              width:100%;
              margin:0 auto
        }
        div {
          
            height:104px;
            width:321px;
            margin:13px auto 0;
        
            h3{
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                width: 100%;
                margin: 0 auto;
                font-size: 16px;
                font-weight:400;
                color: #333333;
                line-height:45px;
                text-align: left;
            }
            p{
                 text-overflow: ellipsis;
                overflow: hidden;
                width: 100%;
                margin: 0 auto;
                font-size: 0.373333rem;
                font-weight: 400;
                color: #666666;
                height: 39px;
                line-height: 20px;
                word-break: keep-all;
                text-align: left;
            }
             
        }
        .showMore{
            width:98px;
            height:25px;
            margin:27px auto;
            border-radius:2px;
            border:1px solid rgba(17,79,203,1);
            font-size:12px;
            font-weight:400;
            color:rgba(17,79,203,1);
            line-height:25px;
            text-align:center
        }      
    }
    .blue{
        height:59px;
        width:100%;
        background:#1251D0;
    }
    .aboutUs{
        height:292px;
        width:90%;
        margin:0 auto;
        background:#fff;
        box-shadow:0px 2px 16px 0px rgba(0,0,0,0.11);
        border-radius:5px;
        .toAboutUs{
            border:1px solid  #114fcb;
            width: 98px;
            height: 25px;
            border-radius:2px;
            text-align: center;
            line-height:25px;
            font-size:12px;
            margin:0 auto;
            a{
                color:#114fcb;
            }
        }
        p{             
            font-size:14px;        
            color:rgba(89,89,89,1);
            line-height:25px;
            margin: 0 11px;
            text-align: left;
        }
          
    }
    .videos{
        height: 210px;
        width: 318px;
        margin: 10px auto;
        video{
            width:100%;
            height:182px;
        }
    }
    .joinUs{
        height:330px;
        width:90%;
        margin:0 auto;
        background:#fff;
        box-shadow:0px 2px 16px 0px rgba(0,0,0,0.11);
        border-radius:5px;
         .showMore{
            width:98px;
            height:25px;
            margin:27px auto;
            border-radius:2px;
            border:1px solid rgba(17,79,203,1);
            font-size:12px;
            font-weight:400;
            color:rgba(17,79,203,1);
            line-height:25px;
            text-align:center
        }   
        .jobContent{
            height:65px;
            border-bottom:1px solid #b1b1b1;
            text-align:left;          
            width:90%;
            margin:10px auto;
            p:first-child{
                color: #000;              
                font-size: 16px;
            }
            p:nth-child(2){
                color:#666666;
                font-size: 14px;
            }
            span{
                float:right;
            }
        }
    }
    .partner{
        text-align:center;
        div{
            display:inline-block;
        }
        img{
            width: 120px;
            height: 55px;
        }
    }
    .contact{
        width:90%;
        margin:0 auto 20px;
        font-family:PingFangSC-Regular;
        font-weight:400;
        font-size:14px;
        .name{
             color:rgba(102,102,102,1);
            line-height:20px;
        }
        .address{
            margin-top:14px;           
            color:rgba(177,177,177,1);
            line-height:16px;
        }
        img{
            width:145px;
            height:145px;
        }
    }

    </style>


