<template>
        <div class='newsContent'>
            <Header  msg="新闻动态"/>
            <img src="../assets/img/news.png" alt="" class='banner' >
            <div>
                <mt-navbar v-model="selected">
                    <mt-tab-item id="1">公司动态</mt-tab-item>
                    <mt-tab-item id="2">科技进展</mt-tab-item>
                    <mt-tab-item id="3">行业政策</mt-tab-item>
                </mt-navbar>
                <mt-tab-container v-model="selected">
                    <mt-tab-container-item id="1">                        
                        <div v-for='news in company' @click="toDetail(news.id,news.type)" >
                            <img :src="news.imageUrl" alt="">
                            <p class='title'>{{news.title}}</p>
                            <p class='desc'>{{news.intro}}</p>
                        </div>         
                        <p class='noMore'>没有更多了</p>              
                    </mt-tab-container-item>
                    <mt-tab-container-item id="2">
                        <div v-for='news in technology' @click="toDetail(news.id,news.type)">
                            <img :src="news.imageUrl" alt="">
                            <p class='title'>{{news.title}}</p>
                            <p class='desc'>{{news.intro}}</p>
                        </div>  
                         <p class='noMore'>没有更多了</p>   
                    </mt-tab-container-item>
                    <mt-tab-container-item id="3">
                        <div v-for='news in industry' @click="toDetail(news.id,news.type)"> 
                            <img :src="news.imageUrl" alt="">
                            <p class='title'>{{news.title}}</p>
                            <p class='desc'>{{news.intro}}</p>
                        </div>  
                         <p class='noMore'>没有更多了</p>   
                    </mt-tab-container-item>
                </mt-tab-container>                     
            </div>
        </div>
</template>
<script>
import { Navbar, TabItem } from 'mint-ui';
import Header from './header'


export default { 
    components: {     
        'Navbar.name': Navbar,
        'TabItem.name': TabItem,
        Header  
    
    },  
    data(){
        return{
            selected:'1',
            company:'',
            technology:'',
            industry:'',
         }
    },
    methods:{
         // 新闻详情页
          toDetail(id,type){ 
               this.$router.push({                        
                    name: 'newsDetail',  
                    query: {   
                         id: id,   
                         type:type
                    
                    }                      
               })

          }  ,
               

      },
    mounted(){
         var that =this;
           this.$axios.get('/api/v1/api/news/list?filter[type]=COMPANY')
              .then(function (response) {                    
                that.company = response.data.data ;
                 console.log(that.company)              
                // if(that.newsType ){      
                //         that.setPaginations(that.newsType)                                             
                //     }else{
                //         that.setPaginations("COMPANY")  
                //     }
            })
            .catch(function (response) {
                console.log(response);
            }); 
            
             
            this.$axios.get('/api/v1/api/news/list?filter[type]=TECHNOLOGY')
              .then(function (response) {                    
                         that.technology = response.data.data ;
                        // if(that.newsType )     
                        // that.setPaginations(that.newsType)       
                
            })
            .catch(function (response) {
                console.log(response);
            });  

            this.$axios.get('/api/v1/api/news/list?filter[type]=INDUSTRY')
              .then(function (response) {                    
                         that.industry = response.data.data ;
                        // if(that.newsType )     
                        // that.setPaginations(that.newsType)       
                
                         
            })
            .catch(function (response) {
                console.log(response);
            });  
 
        

    }
}

</script>
<style  lang="scss">
.newsContent{
    .banner{
        width:100%;
        height:143px;
        margin-top: 40px;
    }
    .mint-navbar{
        border-bottom: 1px solid #CCCCCC;
        a{
            border-right: 1px solid #CCCCCC;
        }
    }
   .mint-tab-container-item{
       div{
           height:251px;
           margin:20px auto; 
           width:90%;
           .title{
                 font-size:16px;
                font-family:PingFangSC-Medium;
                font-weight:500;
                color:rgba(51,51,51,1);
                line-height:23px;
                margin:9px auto 5px;
                text-align: left;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
           }
           .desc{
               font-size:14px;
                font-family:PingFangSC-Light;
                font-weight:300;
                color:rgba(153,153,153,1);
                line-height:20px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                height:40px;
                text-align: left;
           }
       }
        img{
            width:345px;
            height:160px;
        }
        .noMore{
            font-size:12px;
            font-family:PingFangSC-Regular;
            font-weight:400;
            color:rgba(153,153,153,1);
            line-height:16px;
            margin:50px auto
        }
    }
}
</style>


