<template>
  <div class="circular">
    <div class="center">
        <div>   
            <img src="../assets/img/small.png" alt="" class="small">
            <img src="../assets/img/big.png" alt="" class="big">            
        </div>             
        <div style='110px' class="message">{{msg}}</div> 
        <div>                     
            <img src="../assets/img/big.png" alt="" class="big small1" >
            <img src="../assets/img/small.png" alt="" class="small big1">
        </div>         
    </div>    
  </div>
</template>

<script>
export default {
  props: {
    msg: {
        type:String,
    }
  }
}
</script>


<style scoped lang="scss">
.circular {     
        margin: 0 auto;
        width: 100%;      
        // height: 80px;
        // line-height:80px;
        position: relative;
        clear: both;
        margin-top: 34px;     
    .center {
            font-size:20px;
            display:inline-flex;           
            margin:20px auto;
            justify-content:space-around;
         div{
            // flex-grow: 1;
            position:relative;
            align-self: center
         }
         div:nth-child(2){
                // flex-grow: 0 ;
                // margin:0 10px;           
         }

        .small {
                width: 11px;
                vertical-align: middle;               
        }
        .small1{
            left:25px;
        }
        .big{
            width: 16px;
            vertical-align: middle;     
        }
        .big1{
            //   left:0
          } 
        span {
            margin-left: 15px;
            margin-right: 15px;
            margin-top: -6px;
            float: left;        
            font-size: 20px;
            color: #333333;
            letter-spacing: 0;
        }
    } 
    .message{
        max-width:275px;
        padding:0 5px;
    }      
}



</style>



